import React from 'react'
import store from '../../config/store'
import { connect } from 'react-redux'
import { SPRITE_SIZE, MAP_WIDTH, MAP_HEIGHT } from '../../config/constants'
import Icon from 'react-icons-kit'
import {arrowUp} from 'react-icons-kit/fa/arrowUp'
import {arrowDown} from 'react-icons-kit/fa/arrowDown'
import {arrowRight} from 'react-icons-kit/fa/arrowRight'
import {arrowLeft} from 'react-icons-kit/fa/arrowLeft'
import walkSpriteBlue from '../player/player_blue-armor.png'
import walkSprite from '../player/player_walk.png'

function Navigate(props) {
    
    function handleClick(route) {
        
        console.log('route ' + route)
        
        switch(route) {
            case 'north':
                return attemptMove('NORTH')
            case 'south':
                return attemptMove('SOUTH')
            case 'east':
                return attemptMove('EAST')
            case 'west':
                return attemptMove('WEST')
            default:
                console.log(route)
        }

        var myNextTile = []

        function getNewPosition(oldPos, direction) {
            
            switch (direction) {
                case 'WEST':
                    return [ oldPos[0]-SPRITE_SIZE, oldPos[1] ]
                case 'EAST':
                    return [ oldPos[0]+SPRITE_SIZE, oldPos[1] ]
                case 'NORTH':
                    return [ oldPos[0], oldPos[1]-SPRITE_SIZE ]
                case 'SOUTH':
                    return [ oldPos[0], oldPos[1]+SPRITE_SIZE ]
            }
        }

        function getSpriteLocation(direction, walkIndex) {
            switch(direction) {
                case 'SOUTH':
                    return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*0}px`
                case 'EAST':
                    return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*1}px`
                case 'WEST':
                    return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*2}px`
                case 'NORTH':
                    return `${SPRITE_SIZE*walkIndex}px ${SPRITE_SIZE*3}px`
            }
        }

        function getWalkIndex() {
            const walkIndex = store.getState().player.walkIndex
            return walkIndex >= 5 ? 0 : walkIndex + 1
        }

        function observeBoundaries(oldPos, newPos) {
            return (newPos[0] >= 0 && newPos[0] <= MAP_WIDTH - SPRITE_SIZE) &&
                (newPos[1] >= 0 && newPos[1] <= MAP_HEIGHT - SPRITE_SIZE)
        }

        function observeImpassable(oldPos, newPos) {
            const tiles = store.getState().map.tiles
            const y = newPos[1] / SPRITE_SIZE
            const x = newPos[0] / SPRITE_SIZE
            const nextTile = tiles[y][x]
            myNextTile = nextTile
            //return nextTile < 27
            return (nextTile != 17 && nextTile != 18 && nextTile != 19 && nextTile != 9 && nextTile != 10)
        }

        function playSound() {
            //var audio = new Audio({squish})
            //audio.loop = false
            //audio.volume = 0.99
            //audio.play()
        }

        function dispatchMove(direction, newPos) {
            const walkIndex = getWalkIndex()
            
            if(myNextTile==4) {
                direction = 'SOUTH'
                alert('You found the treasure chest!!!')
                if ( store.getState().player.blueArmor == 'true') {
                    store.dispatch({
                        type: 'MOVE_PLAYER',
                        payload: {
                            position: newPos,
                            direction: direction,
                            walkIndex,
                            spriteLocation: getSpriteLocation(direction, walkIndex),
                            backgroundImage: `url('${walkSpriteBlue}')`,
                            blueArmor: 'true',
                        }
                    })
                } else {
                    store.dispatch({
                        type: 'MOVE_PLAYER',
                        payload: {
                            position: newPos,
                            direction: direction,
                            walkIndex,
                            spriteLocation: getSpriteLocation(direction, walkIndex),
                            backgroundImage: `url('${walkSprite}')`,
                            blueArmor: 'false',
                        }
                    })
                }
            } else if(myNextTile==27) {
                direction = 'SOUTH'
                alert('You found the blue armor!!!')
                store.dispatch({
                    type: 'MOVE_PLAYER',
                    payload: {
                        position: newPos,
                        direction: direction,
                        walkIndex,
                        spriteLocation: getSpriteLocation(direction, walkIndex),
                        backgroundImage: `url('${walkSpriteBlue}')`,
                        blueArmor: 'true',
                    }
                })
                //alert('bluearmor ' + store.getState().player.blueArmor)
            } 
            else {
                if ( store.getState().player.blueArmor == 'true') {
                    store.dispatch({
                        type: 'MOVE_PLAYER',
                        payload: {
                            position: newPos,
                            direction: direction,
                            walkIndex,
                            spriteLocation: getSpriteLocation(direction, walkIndex),
                            backgroundImage: `url('${walkSpriteBlue}')`,
                            blueArmor: 'true',
                        }
                    })
                } else {
                    store.dispatch({
                        type: 'MOVE_PLAYER',
                        payload: {
                            position: newPos,
                            direction: direction,
                            walkIndex,
                            spriteLocation: getSpriteLocation(direction, walkIndex),
                            backgroundImage: `url('${walkSprite}')`,
                            blueArmor: 'false',
                        }
                    })
                }
            }
            playSound()
        }
    

        function attemptMove(direction) {
            const oldPos = store.getState().player.position
            const newPos = getNewPosition(oldPos, direction)

            if(observeBoundaries(oldPos, newPos) && observeImpassable(oldPos, newPos))
                dispatchMove(direction, newPos)
            
        }
        
    }
    
    return (
        <div style={{
            position: 'absolute', top: '84%',}}>
            <table>
            <tr><td></td><td><button id='north' style={{color: 'blue'}} onClick={(e) => {handleClick('north')}}><Icon icon={arrowUp} size={18}/></button></td><td></td></tr>
            <tr><td><button id='west' style={{color: 'blue'}} onClick={(e) => {handleClick('west')}}><Icon icon={arrowLeft} size={18}/></button></td><td></td><td><button id='east' style={{color: 'blue'}} onClick={(e) => {handleClick('east')}}><Icon icon={arrowRight} size={18}/></button></td></tr>
            <tr><td></td><td><button id='south' style={{color: 'blue'}} onClick={(e) => {handleClick('south')}}><Icon icon={arrowDown} size={18}/></button></td><td></td></tr>
            </table>
        </div>
    )
}

function mapStateToProps(state) {
    return {
        ...state.navigate,    
    }
}

export default connect(mapStateToProps)(Navigate)




